import React from "react";
import graphic from "../assets/admin-dash.png";
const Admin = () => {
  return (
    <>
      <div className="admin-cards">
        <img src={graphic} className="admin-graphs" alt="" />
        <div className="admin-grid">
          <div className="card w-75" style={{ width: "90%!important" }}>
            <div className="card-body">
              <h5 className="card-title">Blogs Management</h5>
              <p className="card-text">
                Create , Read , Upate and Delete Blogs and Perform Opertions
                like adding and editing blogs
              </p>
              <a href="/admin/manage-blogs" className="btn btn-primary">
                Go to Module
              </a>
            </div>
          </div>
          <div className="card w-75">
            <div className="card-body">
              <h5 className="card-title">Recipe Management</h5>
              <p className="card-text">
                Create , Read , Upate and Delete Blogs and Perform Opertions
                like Adding Editing Recipes
              </p>
              <a href="/admin/manage-recipes" className="btn btn-primary">
                Go to Module
              </a>
            </div>
          </div>
          <div className="card w-75">
            <div className="card-body">
              <h5 className="card-title">Contact Form Leads</h5>
              <p className="card-text">
                Check Who All Users are asking for your help and Suggesting you
                about the website
              </p>
              <a href="/admin/view-contacts" className="btn btn-primary">
                Go to Module
              </a>
            </div>
          </div>
        </div>
      </div>
    </>
  );
};

export default Admin;
