import React, { useState, useEffect } from "react";
import Loader from "../Loader";
import axios from "axios";
import { useParams } from "react-router-dom/cjs/react-router-dom.min";
const RecipeLanding = () => {
  const [data, setData] = useState(null);
  const [data_com, setDataCom] = useState(null);
  const [message, setMessage] = useState("");
  const [f_name, setFname] = useState("");
  const [email, setEmail] = useState("");
  const [success, setSuccess] = useState(null);
  const [error, setError] = useState(null);
  console.log(error);
  const { id } = useParams();
  const jData = {
    submitted_by: f_name,
    submitted_email: email,
    submitted_on: id,
    comment_text: message,
  };
  const onContact = (e) => {
    setSuccess(false);
    e.preventDefault();
    axios
      .post("/store-recipe-comments", jData)
      .then((res) => {
        window.localStorage.setItem("isAuthenticated", true);
        if (res.status === 200) {
          setSuccess(true);
          window.location.reload();
          getComments();
          setError(false);
          setEmail("");
          setMessage("");
          setFname("");
          document.getElementById("contact-form").reset();
        }
      })
      .catch(({ response }) => {
        // setError(response.data.message);
        setSuccess(true);
      });
  };
  const getComments = (id) => {
    setSuccess(false);
    fetch(
      `/get-recipe-comments/${id}`,
      {
        headers: {
          "Cache-Control": "no-cache",
          "Content-Type": "application/json",
          Accept: "application/json",
        },
        method: "GET",
      }
    )
      .then((res) => res.json())
      .then((result) => {
        setDataCom(result);
        setSuccess(true);
      })
      .catch((err) => {
        console.log(err);
      });
  };
  const getData = () => {
    fetch("/recipes/get-recipes", {
      headers: {
        "Cache-Control": "no-cache",
        "Content-Type": "application/json",
        Accept: "application/json",
      },
      method: "GET",
    })
      .then((res) => res.json())
      .then((result) => {
        setData(result);
      })
      .catch((err) => {
        console.log(err);
      });
  };
  useEffect(() => {
    getData();
    getComments(id);
  }, [id]);
  if (!success || data === null) {
    return (
      <>
        <Loader />
      </>
    );
  } else {
    return (
      <>
        <main className="mt-4 mb-5">
          <div className="container">
            <div className="row">
              <div className="col-md-10 mb-4">
                <section className="border-bottom mb-4">
                  <img
                    src={data[id].image_link}
                    className="img-fluid shadow-2-strong rounded-5 mb-4"
                    alt=""
                    style={{
                      width: "100%",
                      height: "600px",
                      objectFit: "cover",
                    }}
                  />

                  <div className="row pb-2 pt-2">
                    <div className="col-lg-12 m-10">
                      <img
                        src={data[id].image_link}
                        className="rounded-5 shadow-1-strong me-2"
                        height="35"
                        alt=""
                        loading="lazy"
                      />
                      &nbsp;
                      <span>
                        Published{" "}
                        <u>{new Date(data[id].timeStamp).toDateString()}</u> by
                      </span>
                      <a href="/#" className="text-dark">
                        {data[id].name}
                      </a>
                    </div>
                  </div>
                </section>

                <section>
                  <p>{data[id].content}</p>
                </section>

                <section className="border-bottom mb-4 pb-4">
                  <div className="row pb-4 pt-4">
                    <div className="col-9">
                      <h3 className="mb-2">
                        <strong>{data[id].title}</strong>
                      </h3>
                      <p>{data[id].short_desc}</p>
                    </div>
                    <div className="col-9">
                      <p className="mb-2">
                        <strong>Method</strong>
                      </p>
                      <p>{data[id].method}</p>
                    </div>
                    <div className="col-9">
                      <p className="mb-2">
                        <strong>Allergy Info</strong>
                      </p>
                      <p>{data[id].allergy_info}</p>
                    </div>
                    <div className="col-9">
                      <p className="mb-2">
                        <strong>Tags</strong>
                      </p>
                      <p>{data[id].tags}</p>
                    </div>
                    <div className="col-9">
                      <p className="mb-2">
                        <strong>Ingredients</strong>
                      </p>
                      <p>{data[id].method}</p>
                    </div>
                  </div>
                </section>

                <section className="border-bottom mb-3">
                  <p>
                    <strong>Comments: {data_com.length}</strong>
                  </p>

                  {data_com ===null ?<></>:data_com.map((comment, index) => {
                    return (
                      <div className="row mb-4" key={index}>
                        <div className="col-2">
                          <img
                            src="https://mdbootstrap.com/img/Photos/Avatars/img%20(24).jpg"
                            className="img-fluid shadow-1-strong rounded-5"
                            alt=""
                            style={{
                              width: "100px",
                              height: "100px",
                              borderRadius: "100%",
                            }}
                          />
                        </div>

                        <div className="col-10">
                          <p className="mb-2">
                            <strong>{comment.submitted_by}</strong>
                          </p>
                          <p>{comment.comment_text}</p>
                        </div>
                      </div>
                    );
                  })}
                </section>

                <section className="col-lg-6 align-flex-end bg-white pt-2 pb-2">
                  <p className="text-center">
                    <strong>Leave a reply</strong>
                  </p>

                  <form
                    onSubmit={(e) => {
                      onContact(e);
                    }}
                  >
                    <div className="form-outline mb-4">
                      <label className="form-label" htmlFor="form4Example1">
                        Name
                      </label>
                      <input
                        type="text"
                        id="form4Example1"
                        className="form-control"
                        onChange={(e) => {
                          setFname(e.target.value);
                        }}
                      />
                    </div>

                    <div className="form-outline mb-4">
                      <label className="form-label" htmlFor="form4Example2">
                        Email address
                      </label>
                      <input
                        type="email"
                        id="form4Example2"
                        className="form-control"
                        onChange={(e) => {
                          setEmail(e.target.value);
                        }}
                      />
                    </div>

                    <div className="form-outline mb-4">
                      <label className="form-label" htmlFor="form4Example3">
                        Comments
                      </label>
                      <textarea
                        className="form-control"
                        id="form4Example3"
                        rows="4"
                        onChange={(e) => {
                          setMessage(e.target.value);
                        }}
                      ></textarea>
                    </div>
                    <button
                      type="submit"
                      className="btn btn-primary btn-block mb-4"
                    >
                      Publish
                    </button>
                  </form>
                </section>
              </div>
            </div>
          </div>
        </main>
      </>
    );
  }
};

export default RecipeLanding;
