import React, { useState } from "react";
import { Link } from "react-router-dom";
import axios from "axios";
import loginImg from "../components/assets/login-graphics.png";
import { Box, Button, TextField, Typography } from "@material-ui/core";

const RegisterScreen = () => {
  const [email, setEmail] = useState(null);
  const [password, setPassword] = useState(null);
  const [first_name, setFirstName] = useState(null);
  const [last_name, setLastName] = useState(null);
  const [success, setSuccess] = useState(null);
  const [error, setError] = useState("");
  // eslint-disable-next-line
  const [role, setRole] = useState("User");
  const onSignup = (e) => {
    e.preventDefault();
    axios
      .post("/auth/register", {
        email,
        password,
        first_name,
        last_name,
        role,
      })
      .then((res) => {
        window.localStorage.setItem("isAuthenticated", true);
        if (res.status === 200) {
          setSuccess(true);
          setError("");
          window.location.href("/");
        }
      })
      .catch(({ response }) => {
        // setError(response.data.message);
        setSuccess(false);
      });
  };
  return (
    <div className="auth-background d-flex">
      <img src={loginImg} alt="" />
      <Box className="auth-box">
        <form
          onSubmit={(e) => {
            onSignup(e);
          }}
        >
          {success && "You've registered in successfully"}
          {error}
          <Typography variant="h5" className="font-weight-bold row">
            Create an account
          </Typography>
          <div className="auth-inputs">
            <div className="auth-field auth-flex">
              <div className="w-50">
                {/* <span className="auth-subtitle">First Name</span> */}
                <TextField
                  fullWidth
                  variant="outlined"
                  name="first_name"
                  placeholder="First Name"
                  style={{ backgroundColor: "#fff", borderRadius: "10px" }}
                  onChange={(e) => {
                    setFirstName(e.target.value);
                  }}
                  required
                />
              </div>
              <div className="w-50 ml-4">
                {/* <span className="auth-subtitle">Last Name</span> */}
                <TextField
                  fullWidth
                  variant="outlined"
                  name="last_name"
                  placeholder="Last Name"
                  style={{ backgroundColor: "#fff", borderRadius: "10px" }}
                  onChange={(e) => {
                    setLastName(e.target.value);
                  }}
                  required
                />
              </div>
            </div>
            <div className="auth-field">
              {/* <span className="auth-subtitle">E-mail Address</span> */}
              <TextField
                type="email"
                fullWidth
                variant="outlined"
                placeholder="Email"
                style={{ backgroundColor: "#fff", borderRadius: "10px" }}
                name="email"
                onChange={(e) => {
                  setEmail(e.target.value);
                }}
                required
              />
            </div>
            <div className="auth-field">
              {/* <span className="auth-subtitle">Password</span> */}
              <TextField
                type="password"
                fullWidth
                placeholder="Password"
                style={{ backgroundColor: "#fff", borderRadius: "10px" }}
                variant="outlined"
                name="password"
                onChange={(e) => {
                  setPassword(e.target.value);
                }}
                required
              />
            </div>
          </div>

          <Button
            fullWidth
            className="mt-4 p-3 submit-button"
            variant="contained"
            type="submit"
          >
            <Typography variant="h6" className="font-weight-bold">
              Create account
            </Typography>
          </Button>
          <p className="mt-2">
            Already have an account? <Link to={"/login"}>Login</Link>
          </p>
        </form>
      </Box>
    </div>
  );
};
export default RegisterScreen;
