import React from "react";
import { useState, useEffect } from "react";
// import place from "./assets/blog-place.jpg";
const Blog = () => {
  const [data, setData] = useState([]);
  const getData = () => {
    fetch("/blogs/get-blogs", {
      headers: {
        "Cache-Control": "no-cache",
        "Content-Type": "application/json",
        Accept: "application/json",
      },
      method: "GET",
    })
      .then((res) => res.json())
      .then((result) => {
        setData(result);
        console.log(data);
      })
      .catch((err) => {
        console.log(err);
      });
  };
  useEffect(() => {
    getData();
    //eslint-disable-next-line
  },[]);
  return (
    <>
      <div id="intro" className="p-5 text-center bg-light">
        <h1 className="mb-3 h2">Welcome to Our Blogs</h1>
        <p className="mb-3">Stay up-to-date with latest in recipes</p>
      </div>

      <main className="my-5">
        <div
          className="container"
          style={{ width: "98vw!important", maxWidth: "98vw!important" }}
        >
          <section className="text-center">
            <h4 className="mb-5">
              <strong>Latest posts</strong>
            </h4>

            <div className="row">
            {data.map((blog,index) => (
            <div className="col-lg-4 col-md-12 mb-4" key={index}>
            <div className="card-blog">
              <div
                className="bg-image hover-overlay ripple"
                data-mdb-ripple-color="light"
              >
                <img src={blog.image_link} className="img-fluid" alt="" />
                <a href="#!">
                  <div
                    className="mask"
                    style={{ backgroundColor: "rgba(251, 251, 251, 0.15)" }}
                  ></div>
                </a>
              </div>
              <div className="card-blog-body text-left p-2">
                <h5 className="card-blog-title">{blog.title}</h5>
                <p className="card-blog-text">
                 {blog.short_desc}
                </p>
                <a href={`/blog-landing/${index}`} className="btn btn-primary w-100">
                  Read
                </a>
              </div>
            </div>
          </div>
          ))}
            </div>
          </section>

          <nav className="my-4" aria-label="...">
            <ul className="pagination pagination-circle justify-content-center">
              <li className="page-item">
                <a
                  className="page-link"
                  href="/"
                  tabindex="-1"
                  aria-disabled="true"
                >
                  Previous
                </a>
              </li>
              <li className="page-item  active">
                <a className="page-link" href="/">
                  1
                </a>
              </li>
              {/* <li className="page-item active" aria-current="page">
                <a className="page-link" href="/">
                  2 <span className="sr-only">(current)</span>
                </a>
              </li>
              <li className="page-item">
                <a className="page-link" href="/">
                  3
                </a>
              </li> */}
              <li className="page-item">
                <a className="page-link" href="/">
                  Next
                </a>
              </li>
            </ul>
          </nav>
        </div>
      </main>
    </>
  );
};

export default Blog;
