import React, { useState, useEffect } from "react";
import Loading from "../../Loader";
const UpdateBlog = () => {
  const [title, setTitle] = useState("");
  const [tags, setTags] = useState("");
  const [short_desc, setShortDesc] = useState("");
  const [imageLink, setImageLink] = useState("");
  const [name, setName] = useState("");
  const [l_name, setLname] = useState("");
  const [content, setContent] = useState("");
  const [modal, setModal] = useState(false);
  const [id, setId] = useState(false);
  const [loading, setLoading] = useState(false);
  const jData = {
    title: title,
    tags: tags,
    short_desc: short_desc,
    image_link: imageLink,
    name: name,
    l_name: l_name,
    content: content,
  };
  const [data, setData] = useState([]);
  const getData = () => {
    fetch("/blogs/get-blogs", {
      headers: {
        "Cache-Control": "no-cache",
        "Content-Type": "application/json",
        Accept: "application/json",
      },
      method: "GET",
    })
      .then((res) => res.json())
      .then((result) => {
        setData(result);
        setLoading(false);
        console.log(data);
      })
      .catch((err) => {
        console.log(err);
      });
  };
  const saveNote = (id) => {
    console.log(jData);
    if (jData.tags === "" && jData.title === "") {
    } else {
      var xhr = new XMLHttpRequest();
      xhr.open("POST", `/blogs/update-blog/${id}`);
      xhr.setRequestHeader("Content-Type", "application/json");
      xhr.send(JSON.stringify(jData));
      window.location.reload();
    }
  };
  const editBlog=(id)=>{
    setTitle(data[id].title);
    setImageLink(data[id].image_link);
    setTags(data[id].tags);
    setName(data[id].name);
    setShortDesc(data[id].short_desc)
    setLname(data[id].l_name);
    setContent(data[id].content);


  }
  useEffect(() => {
    getData();
    // eslint-disable-next-line
  }, []);
    if (loading) {
    return <Loading />;
  } else {
  return (
    <>
      <div className="row">
        {data.map((blog, index) => (
          <div className="col-lg-4 col-md-12 mb-4" key={index}>
            <div className="card-blog">
              <div
                className="bg-image hover-overlay ripple"
                data-mdb-ripple-color="light"
              >
                <img src={blog.image_link} className="img-fluid" alt="" />
                <a href="#!">
                  <div
                    className="mask"
                    style={{ backgroundColor: "rgba(251, 251, 251, 0.15)" }}
                  ></div>
                </a>
              </div>
              <div className="card-blog-body text-left p-2">
                <h5 className="card-blog-title">{blog.title}</h5>
                <p className="card-blog-text">{blog.short_desc}</p>
                <button
                  onClick={() => {
                    setModal(true);
                    setId(blog._id);
                    editBlog(index);
                  }}
                  className="btn btn-primary w-100"
                >
                  Edit Blog
                </button>
              </div>
            </div>
          </div>
        ))}
      </div>
      <div
        className="card col-md-4 w-100 "
        style={{ padding: "20px 15px", display: modal ? "block" : "none" }}
      >
        <div className="row">
          <div className="col">
            <div className="form-outline">
              <label className="form-label" htmlFor="form6Example1">
                Author First Name
              </label>
              <input
                type="text"
                id="form6Example1"
                className="form-control"
                value={name}
                onChange={(e) => {
                  setName(e.target.value);
                }}
              />
            </div>
          </div>
          <div className="col">
            <div className="form-outline">
              <label className="form-label" htmlFor="form6Example2">
                Author Last Name
              </label>
              <input
                type="text"
                id="form6Example2"
                value={l_name}
                className="form-control"
                onChange={(e) => {
                  setLname(e.target.value);
                }}
              />
            </div>
          </div>
        </div>
        <div className="form-outline ">
          <label className="form-label" htmlFor="form6Example3">
            Title of Blog
          </label>
          <input
            type="text"
            id="form6Example3"
            className="form-control"
            value={title}
            onChange={(e) => {
              setTitle(e.target.value);
            }}
          />
        </div>

        <div className="form-outline ">
          <label className="form-label" htmlFor="form6Example4">
            Short Description
          </label>
          <input
            type="text"
            id="form6Example4"
            className="form-control"
            value={short_desc}
            onChange={(e) => {
              setShortDesc(e.target.value);
            }}
          />
        </div>

        <div className="form-outline ">
          <label className="form-label" htmlFor="form6Example5">
            Tags
          </label>
          <input
            type="email"
            id="form6Example5"
            className="form-control"
            value={tags}
            onChange={(e) => {
              setTags(e.target.value);
            }}
          />
        </div>

        <div className="form-outline ">
          <label className="form-label" htmlFor="form6Example6">
            Image
          </label>
          <input
            type="text"
            id="form6Example6"
            className="form-control"
            value={imageLink}
            onChange={(e) => {
              setImageLink(e.target.value);
            }}
          />
        </div>

        <div className="form-outline mb-2">
          <label className="form-label" htmlFor="form6Example7">
            Content
          </label>
          <textarea
            className="form-control"
            id="form6Example7"
            rows="4"
            value={content}
            onChange={(e) => {
              setContent(e.target.value);
            }}
          ></textarea>
        </div>
        <button
          className="btn btn-primary btn-block"
          onClick={() => {
            saveNote(id);
          }}
        >
          Add Blog
        </button>
      </div>
    </>
  );
}
};
export default UpdateBlog;
