import React, { useState, useEffect } from "react";
import axios from "axios";
import Loader from "../Loader";
import { Buffer } from "buffer";
import { Box, Button, TextField, Typography } from "@material-ui/core";

const ProfileEdit = () => {
  const [imgs, setImgs] = useState(undefined);
  const [imageBlob, setImageBlob] = useState(null);
  const [imageBlob2, setImageBlob2] = useState(null);
  const postImages = (files) => {
    const objectURL = URL.createObjectURL(files[0]);
    setImgs(objectURL);
    let reader = new FileReader();
    reader.readAsDataURL(files[0]);
    reader.onload = function () {
      setImageBlob(reader.result);
    };
    reader.onerror = function (error) {
      console.log("Error: ", error);
    };
  };
  const [success, setSuccess] = useState(false);
  const [loading, setLoading] = useState(true);
  // eslint-disable-next-line
  const [data, setData] = useState([]);
  const [first_name, setFirstName] = useState(null);
  const [last_name, setLastName] = useState(null);
  const [allergy, setAllergy] = useState(null);
  const jData = {
    first_name: first_name,
    last_name: last_name,
    role: data.role,
    email: data.email,
    allergy_info: allergy,
    image: imageBlob,
    password: data.password,
  };
  const onContact = (e) => {
    setSuccess(false);
    setLoading(true);
    e.preventDefault();
    axios
      .post("/user/setDetails", jData)
      .then((res) => {
        if (res.status === 200) {
          setSuccess(true);
          setLoading(false);
          window.location.reload();
        }
      })
      .catch(({ response }) => {
        // setError(response.data.message);
        setSuccess(true);
      });
  };
  useEffect(() => {
    axios
      .get("/user/getDetails")
      .then(
        ({
          data: {
            first_name,
            last_name,
            role,
            email,
            allergy_info,
            password,
            image,
          },
        }) => {
          setData({
            first_name,
            last_name,
            role,
            email,
            allergy_info,
            password,
          });
          setFirstName(first_name);
          setLastName(last_name);
          setAllergy(allergy_info);
          setImageBlob2(image);
          setLoading(false);
        }
      )
      .catch((err) => console.log(err));
  }, []);
  if (loading) {
    return <Loader />;
  } else {
    return (
      <div className="auth-background d-flex">
        {/* <img src={loginImg} alt="" /> */}
        <Box className="auth-box">
          <form
            onSubmit={(e) => {
              onContact(e);
            }}
          >
            {success ? "Data Updated successfully" : ""}
            <Typography variant="h5" className="font-weight-bold row">
              Edit Profile Details
            </Typography>
            <div className="auth-inputs">
              <div className="auth-field" style={{ display: "flex" }}>
                <div className="w-50">
                  {imageBlob2 === null ? (
                    <></>
                  ) : (
                    <img
                      src={new Buffer.from(imageBlob2).toString("ascii")}
                      alt=""
                    />
                  )}
                </div>
                <div className="w-50">
                  <img src={imgs} alt="" className="floatImage" />
                </div>
              </div>
              <div className="auth-field">
                <input
                  type="file"
                  accept="image/*"
                  id="memoImageInput"
                  onChangeCapture={(e) => {
                    postImages(e.target.files);
                  }}
                />
              </div>
              <div className="auth-field auth-flex">
                <div className="w-50">
                  {/* <span className="auth-subtitle">First Name</span> */}
                  <TextField
                    fullWidth
                    variant="outlined"
                    name="first_name"
                    value={first_name}
                    placeholder="First Name"
                    style={{ backgroundColor: "#fff", borderRadius: "10px" }}
                    onChange={(e) => {
                      setFirstName(e.target.value);
                    }}
                    required
                  />
                </div>
                <div className="w-50 ml-4">
                  {/* <span className="auth-subtitle">Last Name</span> */}
                  <TextField
                    fullWidth
                    variant="outlined"
                    name="last_name"
                    value={last_name}
                    placeholder="Last Name"
                    style={{ backgroundColor: "#fff", borderRadius: "10px" }}
                    onChange={(e) => {
                      setLastName(e.target.value);
                    }}
                    required
                  />
                </div>
              </div>
              <div className="auth-field">
                {/* <span className="auth-subtitle">E-mail Address</span> */}
                <TextField
                  type="email"
                  fullWidth
                  disabled
                  variant="outlined"
                  placeholder="Email"
                  style={{ backgroundColor: "#fff", borderRadius: "10px" }}
                  name="email"
                  value={data.email}
                />
              </div>
              <div className="auth-field">
                {/* <span className="auth-subtitle">Password</span> */}
                <TextField
                  type="text"
                  fullWidth
                  value={allergy}
                  placeholder="Allergy Information"
                  style={{ backgroundColor: "#fff", borderRadius: "10px" }}
                  variant="outlined"
                  name="allergy_info"
                  onChange={(e) => {
                    setAllergy(e.target.value);
                  }}
                  required
                />
              </div>
            </div>

            <Button
              fullWidth
              className="mt-4 p-3 submit-button"
              variant="contained"
              type="submit"
            >
              <Typography variant="h6" className="font-weight-bold">
                Update Profile
              </Typography>
            </Button>
          </form>
        </Box>
      </div>
    );
  }
};
export default ProfileEdit;
