import React from "react";
import { Link } from "@material-ui/core";
import logo from "./assets/footer-logo.png";
const Footer = () => {
  return (
    <footer className="text-lg-start bg-light text-muted">
      <section className="d-flex justify-content-center justify-content-lg-between p-4 border-bottom border-top">
        <div className="me-5 d-none d-lg-block">
          <span>Get connected with us on social networks:</span>
        </div>

        <div>
          <Link href="#" className="me-4 text-reset">
            <i style={{ margin: "0 5px" }} className="fab fa-facebook-f"></i>
          </Link>
          <Link href="#" className="me-4 text-reset">
            <i style={{ margin: "0 5px" }} className="fab fa-twitter"></i>
          </Link>
          <Link href="#" className="me-4 text-reset">
            <i style={{ margin: "0 5px" }} className="fab fa-instagram"></i>
          </Link>
          <Link href="#" className="me-4 text-reset">
            <i style={{ margin: "0 5px" }} className="fab fa-linkedin"></i>
          </Link>
        </div>
      </section>

      {/* <section className=""> */}
        <div
          className="text-md-start mt-5"
          style={{ width: "98vw" }}
        >
          <div className="row mt-3" style={{margin:'0px'}}>
            <div className="col-md-3 col-lg-4 col-xl-3 mx-auto mb-4">
              <img
                src={logo}
                alt=""
                style={{
                  width: "240px",
                  height: "200px",
                  objectFit: "contain",
                }}
              />
              {/* <h6 className="text-uppercase fw-bold mb-4">
                Central Recipe Generator
              </h6> */}
              {/* <p>
                Here you can use rows and columns to organize your footer
                content. Lorem ipsum dolor sit amet, consectetur adipisicing
                elit.
              </p> */}
            </div>
            <div className="col-md-3 col-lg-2 col-xl-2 mx-auto mb-4">
              <h6 className="text-uppercase fw-bold mb-4">Useful links</h6>
              <p>
                <Link href="/" className="text-reset">
                  Home
                </Link>
              </p>
              <p>
                <Link href="/blog-archive" className="text-reset">
                  Blogs
                </Link>
              </p>
              <p>
                <Link href="/about-us" className="text-reset">
                  About Us
                </Link>
              </p>
              <p>
                <Link href="contact-us" className="text-reset">
                  Contact Us
                </Link>
              </p>
            </div>

            <div className="col-md-4 col-lg-3 col-xl-3 mx-auto mb-md-0 mb-4">
              <h6 className="text-uppercase fw-bold mb-4">Contact</h6>
              <p>
                <i style={{ margin: "0 5px" }} className="fas fa-home me-3"></i>
                New York, NY 10012, US
              </p>
              <p>
                <i
                  style={{ margin: "0 5px" }}
                  className="fas fa-envelope me-3"
                ></i>
                info@example.com
              </p>
              <p>
                <i
                  style={{ margin: "0 5px" }}
                  className="fas fa-phone me-3"
                ></i>
                + 01 234 567 88
              </p>
              {/* <p>
                <i
                  style={{ margin: "0 5px" }}
                  className="fas fa-print me-3"
                ></i>
                + 01 234 567 89
              </p> */}
            </div>
          </div>
        </div>
      {/* </section> */}

      <div
        className=" p-4"
        style={{ backgroundColor: "#144343",color:'#fff', textAlign: "center" }}
      >
        <span className="text-reset fw-bold">
          © Central Recipe Generator 2023
        </span>
      </div>
    </footer>
  );
};

export default Footer;
