import React, { useState } from "react";
import axios from "axios";
import loginImg from "../components/assets/contact-us.png";
import {
  Box,
  Button,
  TextField,
  TextareaAutosize,
  Typography,
} from "@material-ui/core";

const ContactUs = () => {
  const [message, setMessage] = useState("");
  const [f_name, setFname] = useState("");
  const [l_name, setLname] = useState("");
  const [phone, setPhone] = useState("");
  const [email, setEmail] = useState("");
  const [success, setSuccess] = useState(null);
  const [error, setError] = useState(null);
  const jData = {
    name: f_name,
    l_name: l_name,
    email: email,
    phone: phone,
    message: message,
  };
   const onContact = (e) => {
     e.preventDefault();
     axios
       .post("/contact-users", jData)
       .then((res) => {
         window.localStorage.setItem("isAuthenticated", true);
         if (res.status === 200) {
           setSuccess(true);
           setError(false);
           setEmail("");
           setMessage("");
           setFname("");
           setLname("");
           setPhone("");
           document.getElementById("contact-form").reset();
         }
       })
       .catch(({ response }) => {
         setError(response.data.message);
         setSuccess(false);
       });
   };
  return (
    <div className="auth-background d-flex" style={{margin:'50px 0px'}}>
      <img src={loginImg} alt="" />
      <Box className="auth-box">
        <form
        id="contact-form"
          onSubmit={(e) => {
            onContact(e);
          }}  
        >
          {success && "Form Submitted Successfully!"}
          {error}
          <Typography variant="h5" className="font-weight-bold row">
            Contact Us
          </Typography>
          <div className="auth-inputs">
            <div className="auth-field auth-flex">
              <div className="w-50">
                {/* <span className="auth-subtitle">First Name</span> */}
                <TextField
                  fullWidth
                  variant="outlined"
                  name="first_name"
                  placeholder="First Name"
                  style={{ backgroundColor: "#fff" }}
                  onChange={(e) => {
                    setFname(e.target.value);
                  }}
                  required
                />
              </div>
              <div className="w-50 ml-4">
                {/* <span className="auth-subtitle">Last Name</span> */}
                <TextField
                  fullWidth
                  variant="outlined"
                  name="last_name"
                  placeholder="Last Name"
                  style={{ backgroundColor: "#fff" }}
                  onChange={(e) => {
                    setLname(e.target.value);
                  }}
                  required
                />
              </div>
            </div>

            <div className="auth-field">
              {/* <span className="auth-subtitle">E-mail Address</span> */}
              <TextField
                type="email"
                fullWidth
                variant="outlined"
                placeholder="Email"
                style={{ backgroundColor: "#fff" }}
                name="email"
                onChange={(e) => {
                  setEmail(e.target.value);
                }}
                required
              />
            </div>
            <div className="auth-field">
              {/* <span className="auth-subtitle">Password</span> */}
              <TextField
                type="phone"
                fullWidth
                variant="outlined"
                placeholder="Phone Number"
                style={{ backgroundColor: "#fff" }}
                name="email"
                onChange={(e) => {
                  setPhone(e.target.value);
                }}
                required
              />
            </div>
            <div className="auth-field">
              {/* <span className="auth-subtitle">Password</span> */}
              <TextareaAutosize
                aria-label="minimum height"
                minRows={5}
                type="text"
                fullWidth
                placeholder="Message"
                style={{ backgroundColor: "#fff" ,padding:'18.5px 14px', width:'100%',border:'1px solid rgba(128, 128, 128, 0.458)',borderRadius:'4px'}}
                name="email"
                onChange={(e) => {
                  setMessage(e.target.value);
                }}
                required
              />
            </div>
          </div>

          <Button
            fullWidth
            className="mt-4 p-3 submit-button"
            variant="contained"
            type="submit"
          >
            <Typography variant="h6" className="font-weight-bold">
              Submit
            </Typography>
          </Button>
        </form>
      </Box>
    </div>
  );
};
export default ContactUs;
