import React, { useEffect, useState } from "react";
import axios from "axios";
import { Route, Switch } from "react-router-dom";
import "./styles/Auth.css";
import Home from "./components/Home";
import Register from "./components/Register";
import Login from "./components/Login";
import ForgotPass from "./components/ForgotPass";
import ContactUs from "./components/ContactUs";
import AboutUs from "./components/AboutUs";
import NotFound from "./components/NotFound";
import Header from "./components/Header";
import Footer from "./components/Footer";
import Blog from "./components/Blog";
import AddBlog from "./components/Admin/AddBlog";
import AddRecipe from "./components/Admin/AddRecipe";
import BlogLanding from "./components/BlogLanding";
import Admin from "./components/Admin/Admin";
import AdminBlogs from "./components/Admin/AdminBlogs";
import AdminRecipes from "./components/Admin/AdminRecipes";
import RecipeLanding from "./components/RecipeLanding";
import ViewContacts from "./components/Admin/ViewContacts";
import ViewBlogs from "./components/Admin/ViewBlogs";
import UpdateBlog from "./components/Admin/UpdateBlog";
import UpdateRecipes from "./components/Admin/UpdateRecipes";
import DeleteRecipes from "./components/Admin/DeleteRecipes";
import ViewRecipes from "./components/Admin/ViewRecipes";
import DeleteBlogs from "./components/Admin/DeleteBlogs";
import Search from "./components/Search";
import ProfileEdit from "./components/ProfileEdit";
const App = () => {
  const [data, setData] = useState(null);
  // const [loading, setLoading] = useState(true);
  useEffect(() => {
    axios
      .get("/user/getDetails")
      .then(({ data: { first_name, last_name, role } }) => {
        setData({ first_name, last_name, role });
        // setLoading(false);
      })
      .catch((err) => console.log(err));
  }, []);
  if (data === null) {
    return (
      <Switch>
        <Route exact path='/register' component={Register} />
        <Route exact component={Login} />
      </Switch>
    );
  } else if (data.role === "Admin") {
    return (
      <>
        <Header data={data} />
        <Switch>
          <Route exact path="/" component={Admin} />
          <Route exact path="/admin/admin-login" component={Login} />
          <Route exact path="/admin/manage-blogs" component={AdminBlogs} />
          <Route exact path="/admin/add-blogs" component={AddBlog} />
          <Route exact path="/admin/view-blogs" component={ViewBlogs} />
          <Route exact path="/admin/update-blogs" component={UpdateBlog} />
          <Route exact path="/admin/delete-blogs" component={DeleteBlogs} />
          <Route exact path="/admin/add-recipes" component={AddRecipe} />
          <Route exact path="/admin/update-recipes" component={UpdateRecipes} />
          <Route exact path="/admin/delete-recipes" component={DeleteRecipes} />
          <Route exact path="/admin/view-recipes" component={ViewRecipes} />
          <Route exact path="/admin/manage-recipes" component={AdminRecipes} />
          <Route exact path="/admin/view-contacts" component={ViewContacts} />
          <Route exact path="/blog-landing/:id" component={BlogLanding} />
          <Route exact path="/recipe-landing/:id" component={RecipeLanding} />
          <Route exact component={NotFound} />
        </Switch>
      </>
    );
  } else {
    return (
      <>
        <Header data={data} />
        <Switch>
          <Route exact path="/" component={Home} />
          <Route exact path="/login" component={Login} />
          <Route exact path="/register" component={Register} />
          <Route exact path="/forgot-password" component={ForgotPass} />
          <Route exact path="/contact-us" component={ContactUs} />
          <Route exact path="/about-us" component={AboutUs} />
          <Route exact path="/blog-archive" component={Blog} />
          <Route exact path="/blog-landing/:id" component={BlogLanding} />
          <Route exact path="/recipe-landing/:id" component={RecipeLanding} />
          <Route exact path="/search/" component={Search} />
          <Route exact path="/user/edit-profile/" component={ProfileEdit} />
          <Route exact component={NotFound} />
        </Switch>
        <Footer />
      </>
    );
  }
};
export default App;
