import React, { useState, useEffect } from "react";

const DeleteRecipes = () => {
  const [data, setData] = useState([]);
  const getData = () => {
    fetch("/recipes/get-recipes", {
      headers: {
        "Cache-Control": "no-cache",
        "Content-Type": "application/json",
        Accept: "application/json",
      },
      method: "GET",
    })
      .then((res) => res.json())
      .then((result) => {
        setData(result);
        console.log(data);
      })
      .catch((err) => {
        console.log(err);
      });
  };
  const deleteData = (docId) => {
    fetch(`/recipes/delete-recipe/${docId}`, {
      headers: {
        "Cache-Control": "no-cache",
        "Content-Type": "application/json",
        Accept: "application/json",
      },
      method: "DELETE",
    })
      .then((res) => res.json())
      .then((result) => {
        setData(result);
        getData();
        window.location.href = "/admin/manage-recipes";
      })
      .catch((err) => {
        console.log(err);
      });
  };
  useEffect(() => {
    getData();
    //react-hooks/exhaustive-deps
  }, []);
  return (
    <div className="row">
      {data.map((blog, index) => (
        <div className="col-lg-4 col-md-12 mb-4" key={index}>
          <div className="card-blog">
            <div
              className="bg-image hover-overlay ripple"
              data-mdb-ripple-color="light"
            >
              <img src={blog.image_link} className="img-fluid" alt="" />
              <a href="#!">
                <div
                  className="mask"
                  style={{ backgroundColor: "rgba(251, 251, 251, 0.15)" }}
                ></div>
              </a>
            </div>
            <div className="card-blog-body text-left p-2">
              <h5 className="card-blog-title">{blog.title}</h5>
              <p className="card-blog-text">{blog.short_desc}</p>
              <button
                onClick={() => {
                  deleteData(data[index]._id);
                }}
                className="btn btn-primary w-100"
              >
                Delete
              </button>
            </div>
          </div>
        </div>
      ))}
    </div>
  );
};

export default DeleteRecipes;
