import React from "react";
import { useState, useEffect } from "react";
import "../styles/Home.css";
// import place from "./assets/card-place.jpg";
const Home = () => {
  const [data, setData] = useState([]);
  const getData = () => {
    fetch("/recipes/get-recipes", {
      headers: {
        "Cache-Control": "no-cache",
        "Content-Type": "application/json",
        Accept: "application/json",
      },
      method: "GET",
    })
      .then((res) => res.json())
      .then((result) => {
        setData(result);
        console.log(data);
      })
      .catch((err) => {
        console.log(err);
      });
  };
  useEffect(() => {
    getData();
    //es-lint-disable-next-line
  }, []);
  return (
    <>
      <div className="hero">
        <h1>Welcome to Central Recipe Generator</h1>
        <div className="input-holder">
          <form method="get" action="/search" style={{textAlign:'center',width:'100%'}}>
            <input placeholder="Enter Your Search Keywords" type="text" name="search" />
            <button type="submit" className="btn btn-primary">
              Search
            </button>
          </form>
        </div>
      </div>
      <div
        className="row mt-4"
        style={{ justifyContent: "center", alignItems: "center" }}
      >
        {data.map((recipe, index) => (
          <div className="card" key={index}>
            <img
              src={`/assets/recipe-${index + 1}.jpeg`}
              className="card-img-top"
              alt="Fissure in Sandstone"
            />
            <div className="card-body">
              <h5 className="card-title">{recipe.title}</h5>
              <p className="card-text">{recipe.short_desc}</p>
              <a href={`/recipe-landing/${index}`} className="btn btn-primary">
                Read Now
              </a>
            </div>
          </div>
        ))}
      </div>
    </>
  );
};

export default Home;
