import React, { useEffect, useState } from "react";
import axios from "axios";

const Search = () => {
  let params = new URLSearchParams(window.location.search);
  const [myParam, setQuery] = useState(params.get("search"));
  const [data, setData] = useState([]);
  var tempData = data.filter((item) => {
    return item.title.includes(myParam);
  });
  const [mainData, setMainData] = useState(tempData);

  // const getData = () => {
  //   fetch(
  //     `/recipes/get-recipes/search/${myParam}`,
  //     {
  //       headers: {
  //         "Cache-Control": "no-cache",
  //         "Content-Type": "application/json",
  //         Accept: "application/json",
  //       },
  //       method: "GET",
  //     }
  //   )
  //     .then((res) => res.json())
  //     .then((result) => {
  //       setData(result);
  //       console.log(data);
  //     })
  //     .catch((err) => {
  //       console.log(err);
  //     });
  // };

  useEffect(() => {
    const fetchData = async () => {
      try {
        const result = await axios.post(`/recipes/search/${myParam}`);
        setData(result.data);
      } catch(err) {console.log(err)}
    };

    fetchData();
  }, [myParam]);
  return (
    <>
      <div className="container">
        <div className="row" id="search">
          <form id="search-form" className="d-flex w-100">
            <div className="form-group col-md-9">
              <input
                value={myParam}
                className="form-control"
                type="text"
                onChange={(e) => {
                  setQuery(e.target.value);
                }}
                placeholder="Search"
              />
            </div>
            <div className="form-group col-md-3">
              <button type="" className="btn btn-block btn-primary">
                Search
              </button>
            </div>
          </form>
        </div>
        <div className="row" id="filter">
          <form className="col-md-12 d-flex">
            <div className="form-group col-sm-3 col-xs-6 flex">
              <select
                data-filter="make"
                className="filter-make filter form-control"
              >
                <option value="">Select Cusine</option>
                <option value="">Show All</option>
              </select>
            </div>
            <div className="form-group col-sm-3 col-xs-6">
              <select
                data-filter="model"
                className="filter-model filter form-control"
              >
                <option value="">Select Category</option>
                <option value="">Show All</option>
              </select>
            </div>
            <div className="form-group col-sm-3 col-xs-6">
              <select
                data-filter="type"
                className="filter-type filter form-control"
              >
                <option value="">Select Allergy Type</option>
                <option value="">Show All</option>
              </select>
            </div>
            <div className="form-group col-sm-3 col-xs-6">
              <select
                data-filter="price"
                className="filter-price filter form-control"
              >
                <option value="">Select Duration Type</option>
                <option value="">Show All</option>
              </select>
            </div>
          </form>
        </div>
        <div
          className="row mt-4"
          style={{ justifyContent: "center", alignItems: "center" }}
        >
          {tempData.length <= 0 ? (
            <h1 className="text-center w-100">No Results Found</h1>
          ) : (
            tempData.map((recipe, index) => (
              <div className="card" key={index}>
                <img
                  src={`/assets/recipe-${index + 1}.jpeg`}
                  className="card-img-top"
                  alt="Fissure in Sandstone"
                />
                <div className="card-body">
                  <h5 className="card-title">{recipe.title}</h5>
                  <p className="card-text">{recipe.short_desc}</p>
                  <p className="card-text">
                    <b>Allergy Info:</b> {recipe.allergy_info}
                  </p>
                  <a
                    href={`/recipe-landing/${index}`}
                    className="btn btn-primary"
                  >
                    Read Now
                  </a>
                </div>
              </div>
            ))
          )}
        </div>
        <div className="row" id="products" style={{ height: "500px" }}></div>
      </div>
    </>
  );
};

export default Search;
