import React, { useState } from "react";
import axios from "axios";
import { Link } from "react-router-dom";
import loginImg from "../components/assets/login-graphics.png";
import { Box, Button, TextField, Typography } from "@material-ui/core";

const Login = () => {
  const [email, setEmail] = useState(null);
  const [password, setPassword] = useState(null);
  const [success, setSuccess] = useState(null);
  const [error, setError] = useState(null);
  const login_req = (e) => {
    e.preventDefault();
    axios
      .post("/auth/login", {
        email,
        password,
      })
      .then((res) => {
        window.localStorage.setItem("isAuthenticated", true);
        if (res.status === 200) {
          setSuccess(true);
          setError(false);
          window.location.href = "/";
        }
      })
      .catch(({ response }) => {
        setError(response.data.message);
        setSuccess(false);
      });
  };
  return (
    <>
      <div className="auth-background">
        <form
          onSubmit={(e) => {
            login_req(e);
          }}
        >
          <Box className="auth-box">
            {success?"You've logged in successfully":"Please Login to Proceed"}
            {error}
            <Typography variant="h5" className="font-weight-bold row white">
              Login
            </Typography>
            <div className="auth-inputs">
              <div className="auth-field">
                {/* <span className="auth-subtitle">E-mail Address</span> */}
                <TextField
                  fullWidth
                  variant="outlined"
                  type="email"
                  name="email"
                  onChange={(e) => {
                    setEmail(e.target.value);
                  }}
                  placeholder="Email"
                  style={{ backgroundColor: "#fff" }}
                  required
                />
              </div>
              <div className="auth-field">
                {/* <span className="auth-subtitle">Password</span> */}
                <TextField
                  fullWidth
                  variant="outlined"
                  type="password"
                  name="password"
                  onChange={(e) => {
                    setPassword(e.target.value);
                  }}
                  placeholder="Password"
                  style={{ backgroundColor: "#fff" }}
                  required
                />
              </div>
            </div>
            <Button
              fullWidth
              type="submit"
              className="mt-4 p-3 submit-button"
              variant="contained"
            >
              <Typography variant="h6" className="font-weight-bold">
                Login
              </Typography>
            </Button>
            <p className="mt-2">
              Don’t have an account?
              <Link to={"/register"}> Sign Up</Link>
            </p>
          </Box>
        </form>
        <img src={loginImg} alt="" />
      </div>
    </>
  );
};

export default Login;
