import React, { useState } from "react";
import Loading from '../../Loader'
import graphic from "../assets/blog-page.png";
const AddBlog = () => {
  const [title, setTitle] = useState(null);
  const [tags, setTags] = useState(null);
  const [short_desc, setShortDesc] = useState(null);
  const [imageLink, setImageLink] = useState(null);
  const [name, setName] = useState(null);
  const [l_name, setLname] = useState(null);
  const [content, setContent] = useState(null);
  const [loading, setLoading] = useState(false);
  const jData = {
    title: title,
    tags: tags,
    short_desc: short_desc,
    image_link: imageLink,
    name: name,
    l_name: l_name,
    content: content,
  };
  const saveNote = () => {
    setLoading(true);
    if (jData.tags === null && jData.title === null) {
    } else {
      var xhr = new XMLHttpRequest();
      xhr.open(
        "POST",
        "/blogs/post-blog"
      );
      xhr.setRequestHeader("Content-Type", "application/json");
      xhr.send(JSON.stringify(jData));
      window.location.href = "/admin/manage-blogs";
    }
  };
  if (loading) {
    return(<Loading/>)
  } else {
    return (
      <>
        <div className="row" style={{ justifyContent: "space-evenly" }}>
          <img src={graphic} className="admin-graphs" alt="" />
          <div className="card col-md-4 w-50 " style={{ padding: "20px 15px" }}>
            <div className="row">
              <div className="col">
                <div className="form-outline">
                  <label className="form-label" htmlFor="form6Example1">
                    Author First Name
                  </label>
                  <input
                    type="text"
                    id="form6Example1"
                    className="form-control"
                    onChange={(e) => {
                      setName(e.target.value);
                    }}
                  />
                </div>
              </div>
              <div className="col">
                <div className="form-outline">
                  <label className="form-label" htmlFor="form6Example2">
                    Author Last Name
                  </label>
                  <input
                    type="text"
                    id="form6Example2"
                    className="form-control"
                    onChange={(e) => {
                      setLname(e.target.value);
                    }}
                  />
                </div>
              </div>
            </div>
            <div className="form-outline ">
              <label className="form-label" htmlFor="form6Example3">
                Title of Blog
              </label>
              <input
                type="text"
                id="form6Example3"
                className="form-control"
                onChange={(e) => {
                  setTitle(e.target.value);
                }}
              />
            </div>

            <div className="form-outline ">
              <label className="form-label" htmlFor="form6Example4">
                Short Description
              </label>
              <input
                type="text"
                id="form6Example4"
                className="form-control"
                onChange={(e) => {
                  setShortDesc(e.target.value);
                }}
              />
            </div>

            <div className="form-outline ">
              <label className="form-label" htmlFor="form6Example5">
                Tags
              </label>
              <input
                type="email"
                id="form6Example5"
                className="form-control"
                onChange={(e) => {
                  setTags(e.target.value);
                }}
              />
            </div>

            <div className="form-outline ">
              <label className="form-label" htmlFor="form6Example6">
                Image
              </label>
              <input
                type="text"
                id="form6Example6"
                className="form-control"
                onChange={(e) => {
                  setImageLink(e.target.value);
                }}
              />
            </div>

            <div className="form-outline mb-2">
              <label className="form-label" htmlFor="form6Example7">
                Content
              </label>
              <textarea
                className="form-control"
                id="form6Example7"
                rows="4"
                onChange={(e) => {
                  setContent(e.target.value);
                }}
              ></textarea>
            </div>
            <button
              className="btn btn-primary btn-block"
              onClick={() => {
                saveNote();
              }}
            >
              Add Blog
            </button>
          </div>
        </div>
      </>
    );
  }
};

export default AddBlog;
